.countdown {
    display: flex;
    justify-content: left;
    margin: 1rem 0 5rem;
    font-weight: 600;

    .timer {
        display: flex;
        flex-direction: column;
        align-items: center;

        span {
            font-size: 4.2rem;

            @include tablet {
                font-size: 3.6rem;
            }
        }

        .timer-text {
            font-size: 1.2rem;
            margin-top: .5rem;
        }
    }
    
    >span {
        margin: 0 1rem;
        font-size: 2.4rem;
        margin-top: .6rem;
    }

    @include tablet {
        justify-content: center;
    }
}