*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  overflow-x: hidden;
  font-family: "Montserrat", sans-serif;
  font-size: 1.5rem;
  background-color: #f3f5f9;
}

a {
  text-decoration: none;
  color: #fff;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

section {
  overflow: hidden;
  width: 100%;
}
section:not(:first-child) {
  padding: 10rem 0;
}
@media screen and (max-width: 768px) {
  section:not(:first-child) {
    padding: 5rem 0;
  }
}

p {
  line-height: 2.4rem;
}

h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 3rem;
}

.container {
  width: 90%;
  max-width: 1150px;
  margin: 0 auto;
  overflow: hidden;
}
@media screen and (max-width: 1024px) {
  .container {
    max-width: 880px;
  }
}

.section-title {
  color: #363b3e;
  text-transform: uppercase;
  padding-bottom: 2rem;
  margin-bottom: 5.5rem;
  text-align: center;
  position: relative;
  font-size: 3.6rem;
}
.section-title.title-secondary {
  text-transform: none;
  text-align: left;
  margin-bottom: 3rem;
}
.section-title.title-secondary::after {
  left: 0;
}
@media screen and (max-width: 768px) {
  .section-title.title-secondary::after {
    left: calc(50% - 30px);
  }
}
@media screen and (max-width: 768px) {
  .section-title.title-secondary {
    text-align: center;
  }
}
.section-title::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 4px;
  width: 65px;
  background: #3f4ce7;
  left: calc(50% - 30px);
}
@media screen and (max-width: 1024px) {
  .section-title {
    font-size: 3rem;
  }
}

.section-title-sec {
  margin-bottom: 2rem;
  color: #fff;
  font-size: 3.6rem;
}

.card-row {
  display: flex;
  justify-content: space-between;
}
.card-row div {
  flex: 1;
  position: relative;
}
@media screen and (max-width: 1024px) {
  .card-row {
    flex-direction: column;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #363b3e;
}

svg {
  pointer-events: none;
}

.hero {
  background: linear-gradient(#1a1b3e, rgba(5, 21, 80, 0.75)), url(../images/bg.jpg) center center;
  background-size: cover;
  color: #fff;
  display: flex;
  position: relative;
  height: 770px;
  padding-top: 8.4rem;
}
.hero .hero-wrapper {
  display: flex;
  align-items: center;
}
.hero .hero-content {
  max-width: 70rem;
  margin: 7rem 0;
}
.hero .hero-content h1 {
  margin-bottom: 2rem;
}
@media screen and (max-width: 1024px) {
  .hero .hero-content h1 {
    font-size: 4.2rem;
  }
}
@media screen and (max-width: 768px) {
  .hero .hero-content h1 {
    font-size: 3.4rem;
  }
}
.hero .hero-content p {
  font-weight: 500;
}
.hero .hero-content .sub-header {
  font-size: 2rem;
  margin-bottom: 3rem;
}
.hero .hero-content .sub-header::before {
  content: "";
  display: inline-block;
  margin-right: 1.5rem;
  background: #fff;
  height: 25px;
  width: 3px;
  vertical-align: bottom;
}
@media screen and (max-width: 768px) {
  .hero .hero-content .sub-header::before {
    display: none;
  }
}
@media screen and (max-width: 1024px) {
  .hero .hero-content .sub-header {
    font-size: 1.8rem;
  }
}
.hero .hero-content .success-message {
  display: flex;
  align-items: center;
}
.hero .hero-content .success-message .check-icon {
  margin-right: 1rem;
}
.hero .hero-content .success-message .confirmation-message {
  font-size: 1.8rem;
  font-weight: 500;
}
@media screen and (max-width: 1024px) {
  .hero .hero-content {
    margin: 6rem 0 18rem;
  }
}
@media screen and (max-width: 768px) {
  .hero .hero-content {
    flex-direction: column;
    text-align: center;
    margin: 6rem 0 12rem;
  }
}
@media screen and (max-width: 1366px) {
  .hero {
    height: calc(100vh + 75px);
  }
}
@media screen and (max-width: 1024px) {
  .hero {
    height: unset;
    padding-top: 6.7rem;
  }
}

.hero-sec {
  position: relative;
}

.hero-sec::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 2;
  background-color: rgba(5, 21, 80, 0.5);
}

.hero-postmaster {
  background: linear-gradient(#1a1b3e, rgba(5, 21, 80, 0.75)), url(../images/postmaster_bg_1.jpg) center center;
  background-size: cover;
  color: #fff;
  display: flex;
  position: relative;
  height: 400px;
  padding-top: 6.4rem;
  justify-content: center;
}
.hero-postmaster h1 {
  color: #fff;
}
.hero-postmaster .hero-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.hero-postmaster .hero-content .sub-header {
  font-size: 2rem;
  font-weight: 500;
}

footer {
  background: #fff;
}
footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
footer .container .social {
  display: flex;
  justify-content: center;
  margin: 1rem 0;
}
footer .container .social a {
  border-radius: 50%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  margin: 0 5px;
  color: #9ba0a3;
  line-height: 45px;
  background: rgba(155, 160, 163, 0.12);
  transition: background 0.2s ease-in-out, color 0.2s ease-in-out;
}
footer .container .social a .social-icon {
  display: block;
}
footer .container .social a:hover {
  color: #9ba0a3;
  background: rgba(155, 160, 163, 0.25);
}
footer .container .copy {
  font-size: 1.2rem;
  color: #555;
}
@media screen and (max-width: 1024px) {
  footer .container .copy {
    padding: 1.5rem;
  }
}
@media screen and (max-width: 1024px) {
  footer .container {
    flex-direction: column-reverse;
  }
}

header {
  position: fixed;
  z-index: 100;
  width: 100%;
  padding: 1rem 0;
}
header.fixed {
  background-color: #fff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
  transition: background 0.2s ease;
}
header.fixed .navbar .nav-link {
  color: #000;
}
header.fixed .logo-white {
  display: none;
}
header.fixed .logo-color {
  display: block;
}
header.fixed .menu-icon {
  fill: #000;
}
header.fixed .sub-menu {
  top: 100%;
  border-radius: 0 0 4px 4px;
}
header .header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .header-wrapper .menu-btn {
  display: none;
  cursor: pointer;
  line-height: 0;
}
header .header-wrapper .menu-btn:hover {
  opacity: 0.7;
}
@media screen and (max-width: 1024px) {
  header .header-wrapper .menu-btn {
    display: block;
  }
}
header .logo-color {
  display: none;
}
@media screen and (max-width: 1024px) {
  header {
    transition: none;
  }
  header.active .nav-list {
    display: block;
  }
}
@media screen and (max-width: 768px) {
  header {
    padding: 1.5rem;
  }
  header .container {
    width: 100%;
  }
  header .logo-white,
header .logo-color {
    width: 114px;
    height: 34px;
  }
}

.navbar .nav-list {
  display: flex;
  align-items: center;
  flex-direction: row;
}
@media screen and (max-width: 768px) {
  .navbar .nav-list .nav-item {
    border-top: 1px solid #e0dee9;
  }
  .navbar .nav-list .nav-item:last-child {
    padding: 20px;
  }
  .navbar .nav-list .nav-item:last-child .btn-header {
    text-align: center;
    width: 100%;
  }
}
.navbar .nav-list .nav-link {
  font-weight: 500;
  font-size: 1.5rem;
  cursor: pointer;
  color: #fff;
  padding: 2rem;
  display: block;
}
.navbar .nav-list .nav-link:hover {
  opacity: 0.8;
}
@media screen and (max-width: 1024px) {
  .navbar .nav-list .nav-link {
    font-size: 1.5rem;
    color: #363b3e;
    padding: 1.6rem 4.8rem;
  }
}
@media screen and (max-width: 768px) {
  .navbar .nav-list .nav-link {
    padding: 1.6rem;
  }
}
.navbar .nav-list div.nav-link {
  display: flex;
}
.navbar .nav-list div.nav-link svg {
  margin-left: 0.5rem;
  vertical-align: middle;
}
@media screen and (max-width: 1024px) {
  .navbar .nav-list div.nav-link {
    justify-content: space-between;
  }
}
@media screen and (max-width: 1024px) {
  .navbar .nav-list {
    display: none;
    left: 0;
    position: absolute;
    width: 100%;
    top: 100%;
    background-color: #fff;
    flex-direction: column;
    transition: display 0.2s ease;
  }
}
.navbar .sub-menu {
  display: none;
  position: absolute;
  background-color: #fff;
  z-index: 999;
  top: 95%;
  padding: 5px 0;
  width: 200px;
  border-radius: 4px;
  box-shadow: 0 4px 9px 0 rgba(67, 65, 79, 0.1);
}
.navbar .sub-menu::before {
  content: "";
  position: absolute;
  top: -6px;
  left: 50%;
  border-radius: 3px;
  transform: translate(-50%, 0) rotate(-315deg);
  width: 17px;
  height: 17px;
  background-color: #fff;
}
.navbar .sub-menu a {
  padding: 12px 20px;
  font-size: 16px;
  line-height: 20px;
  display: block;
  color: #000;
  font-weight: 500;
}
.navbar .sub-menu a:hover {
  opacity: 0.7;
}
@media screen and (max-width: 1024px) {
  .navbar .sub-menu a {
    font-size: 1.5rem;
    padding: 15px 12px 15px 65px;
  }
}
@media screen and (max-width: 768px) {
  .navbar .sub-menu a {
    padding: 15px 12px 15px 35px;
  }
}
.navbar .sub-menu.active {
  display: block;
  max-height: initial;
}
@media screen and (max-width: 1024px) {
  .navbar .sub-menu {
    width: 100%;
    position: relative;
    top: unset;
    max-height: 0;
    display: block;
    overflow: hidden;
    border-radius: 0;
    padding: 0;
    border-top: 1px solid #e6e6e6;
  }
}

.login .logo-color {
  display: none;
}
@media screen and (max-width: 768px) {
  .login .logo-color {
    display: block;
  }
}
@media screen and (max-width: 768px) {
  .login .logo-white {
    display: none;
  }
}

.form-group {
  margin-bottom: 2rem;
}

.input-group {
  display: flex;
  max-width: 500px;
  align-items: center;
}
.input-group .form-input {
  font-size: 1.5rem;
  border-radius: 2px 0 0 2px;
  border: none;
  padding: 1.6rem;
  padding-right: 4.2rem;
  height: 53px;
  box-shadow: none;
  color: #333;
  transition: none;
  outline: none;
  display: block;
  width: 100%;
  background-color: #fff;
  font-weight: 500;
}
@media screen and (max-width: 768px) {
  .input-group .form-input {
    border-radius: 2px;
    margin-bottom: 2rem;
  }
}
@media screen and (max-width: 768px) {
  .input-group {
    flex-direction: column;
    max-width: 100%;
  }
}

.btn {
  display: inline-block;
  background-color: #3f4ce7;
  padding: 1.5rem 3.5rem;
  color: #fff;
  cursor: pointer;
  font-weight: 600;
  font-size: 1.4rem;
  border-radius: 2px;
  transition: all 0.2s ease;
  border: 1px solid transparent;
  font-family: "Montserrat", sans-serif;
}
.btn-rounded {
  border-radius: 0.8rem;
}
.btn.btn-outline {
  background: transparent;
  border-color: #0060e6;
  color: #0060e6;
}
.btn.btn-primary {
  background-color: #2c53a4;
}
.btn.btn-primary:hover {
  background-color: #1f386e;
}
.btn.btn-header {
  padding: 1.3rem 1.7rem;
  font-size: 1.4rem;
}
.btn:hover {
  background: #fff;
  border: 1px solid #fff;
  color: #0060e6;
}
.btn:focus {
  outline: none;
}

@media screen and (max-width: 768px) {
  .btn-hero {
    width: 100%;
    padding: 1.7rem 3rem;
  }
}

.input-group-btn {
  border-radius: 0 2px 2px 0;
  min-width: 162px;
  min-height: 53px;
  padding: 1.5rem 2rem;
  position: relative;
}
.input-group-btn:hover {
  background: #1633ff;
  color: #fff;
}
.input-group-btn.loading {
  color: transparent;
}
.input-group-btn.loading::after {
  content: "";
  animation: spin 0.5s linear infinite;
  position: absolute;
  width: 28px;
  height: 28px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  border: 3px solid #fff;
  border-top-color: transparent;
  border-left-color: transparent;
  border-radius: 50%;
}
@media screen and (max-width: 768px) {
  .input-group-btn {
    border-radius: 2px;
  }
}

.btn-link {
  padding: 0;
  color: #3f4ce7;
  font-weight: 600;
}
.btn-link svg {
  vertical-align: sub;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
.card-list {
  display: flex;
}
.card-list > * + * {
  margin-left: 3.5rem;
}

.card-body {
  position: relative;
  padding: 2.2rem 0;
}
.card-body .c-title {
  padding-bottom: 1.5rem;
  padding-right: 5.5rem;
  font-size: 1.8rem;
  line-height: 1.2;
}
.card-body .c-description {
  color: #666;
  padding-bottom: 2.5rem;
  font-size: 1.4rem;
  line-height: 1.3;
  font-weight: 600;
}
.card-icon {
  width: 4.5rem;
  position: absolute;
  top: 0;
  right: 0;
  padding-left: 1rem;
}
.card-footer {
  display: flex;
  justify-content: flex-start;
}

.form-input {
  font-size: 1.5rem;
  border-radius: 4px;
  background-color: #e2e2e2;
  border: none;
  padding: 1.8rem;
  height: 53px;
  box-shadow: none;
  color: #333;
  outline: none;
  display: block;
  width: 100%;
  font-weight: 500;
}

.form-label {
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 0.8rem;
}

.form-foot {
  padding-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.form-foot .checkbox {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}
.form-foot .checkbox input[type=checkbox] {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.form-foot .checkbox input[type=checkbox]:checked ~ .rec-box {
  background: #3f4ce7;
  border-color: #3f4ce7;
}
.form-foot .checkbox .rec-box {
  width: 18px;
  height: 18px;
  border: 1px solid #6c7370;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  flex-shrink: 0;
  font-size: 11.5px;
}
.form-foot .checkbox .rec-box::after {
  content: "✔";
  color: white;
}
.form-foot .checkbox span {
  color: #777;
  font-size: 15px;
  position: relative;
  font-weight: 400;
}
.form-foot .login-help {
  color: #3f4ce7;
  font-size: 14px;
  text-align: right;
  font-weight: 500;
}

.countdown {
  display: flex;
  justify-content: left;
  margin: 1rem 0 5rem;
